import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { getAllOrdersAction } from '../../store.jsx/products/OrderAction'

export default function Histroy() {
  const { orders } = useSelector(state => state.allOrders)
  const { userlogin } = useSelector(state => state.alluser)
  const dispatch = useDispatch()
  useEffect(() => {
    const promise = dispatch(getAllOrdersAction(userlogin.id))
    return () => {
      promise.abort()
    }
  }, [])

  return <>
    <div className="container-fluid">
      <div className="row">
        <div className="col-12">
          {orders.length == 0
            ? <><h1>Are Kahi Order Tr Kar</h1></>
            : <>
              <table class="table table-dark table-striped table-hover">
                <thead>
                  <tr>
                    <th scope="col">#Order id</th>
                    <th scope="col">Products</th>
                    <th scope="col">Payment</th>
                    <th scope="col">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {orders.map(item => <>
                    <tr>
                      <th>{item.id}</th>
                      <td>
                        <ul class="list-group">
                          {
                            item.products.map(product => <li class="list-group-item d-flex justify-content-between">
                              <img src={product.image} height="50" width="50" alt="" />
                              <span>{product.name}</span>
                              <span>{product.qty}</span>
                              <span>{product.price}/-</span>
                            </li>)
                          }
                        </ul>
                      </td>
                      <td>COD</td>
                      <td>Dispatch</td>
                    </tr>
                  </>)}
                </tbody>
              </table>
            </>}
        </div>
      </div>
    </div>
  </>
}
