import Container from 'react-bootstrap/Container';
import Button from 'react-bootstrap/esm/Button';
import Badge from 'react-bootstrap/esm/Button';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from "react-router-dom"
import { selectCart } from '../store.jsx/products/cartSlice';
import { logout, selectUsers } from '../store.jsx/products/userSlice';

function BasicExample() {
    const dispatch = useDispatch()
    const { cartItems } = useSelector(selectCart)
    const { userlogin } = useSelector(selectUsers)
    return (
        <Navbar bg="dark navbar-dark" expand="lg">
            <Container>
                <Navbar.Brand> <Link to="/" className='nav-link' />E-commerce</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link> <Link to="/" className='nav-link' >Home</Link></Nav.Link>
                        {
                            userlogin
                                ? <>
                                    <NavDropdown title={userlogin.name} id="basic-nav-dropdown">
                                        <NavDropdown.Item >
                                            <Link to="/user/account">Account</Link>
                                        </NavDropdown.Item>
                                        <NavDropdown.Item >
                                            <Link to="/user/history">Order History</Link>
                                        </NavDropdown.Item>
                                        <NavDropdown.Item >
                                            <Link to="/user/profile">Profile</Link>
                                        </NavDropdown.Item>

                                        <NavDropdown.Divider />
                                        <NavDropdown.Item onClick={e => dispatch(logout())}>
                                            LogOut
                                        </NavDropdown.Item>
                                    </NavDropdown>
                                </>

                                :
                                <>
                                    <Nav.Link> <Link to="/login" className='nav-link'>Login</Link></Nav.Link>
                                    <Nav.Link> <Link to="/register" className='nav-link'>Register</Link> </Nav.Link>
                                </>
                        }

                        <Nav.Link> <Link to="/cart" className='nav-link' >Cart{<Badge>{cartItems.length}</Badge>}</Link> </Nav.Link>

                        <Nav.Link> <Link to="/admin/users" className='nav-link' >Users</Link> </Nav.Link>
                        <Nav.Link> <Link to="/admin/products" className='nav-link' >Products</Link></Nav.Link>
                        <Nav.Link> <Link to="/admin/cms" className='nav-link' >Cms</Link></Nav.Link>
                        {/* <Nav.Link> <Link to="/details" className='nav-link' >Details</Link> </Nav.Link> */}
                        {/* <NavDropdown title={<div>Cart <Badge bg="secondary">{cartItems.length}</Badge></div>} id="basic-nav-dropdown" className='nav-link' >
                            <NavDropdown.Item href="#action/3.1">Cart</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">
                                Another action
                            </NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">
                                Separated link
                            </NavDropdown.Item> */}
                        {/* </NavDropdown> */}
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar >
    );
}

export default BasicExample;