import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'

import parse from "html-react-parser"
import { getSingleProductAction } from '../store.jsx/products/productAction'
import addCartAction from '../store.jsx/products/cartAction'
import { addCart } from '../store.jsx/products/cartSlice'

export default function Details() {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const { productId } = useParams()
  const { singleProduct, CartAdded } = useSelector(state => state.allProducts)

  const [qty, setqty] = useState(1)

  useEffect(() => {
    dispatch(getSingleProductAction(productId))


  }, [CartAdded])


  const handleqty = opration => {
    switch (opration) {
      case "inc": setqty(pre => singleProduct.stock == pre ? pre : pre + 1); break;
      case "dec": setqty(pre => pre == 1 ? 1 : pre - 1); break;

      default:
        break;
    }
  }
  const handleAddToCart = () => {
    navigate("/cart")
    dispatch(  addCart({...singleProduct,qty}))

  }


  return <>
    {/* in useeffect first html run and then useefect run thats why we use &&   */}
    <div className="container">
      {singleProduct &&
        <div class="card">

          <div class="card-body">
            <div className="row">
              <div className="col-sm-4">

                <img
                  src={singleProduct.image}
                  alt={singleProduct.name}
                  srcset=""
                  className='img-fluid' />
                <br />

                <div class="btn-group w-100" role="group">

                  {
                    singleProduct.stock == 0
                      ? <h1 className="text-danger">Out Of Stock</h1>
                      : <>
                        <button type="button" onClick={e => handleqty("dec")} class="btn btn-outline-danger btn-outline mx-4">-1</button>
                        <h1>{qty}</h1>
                        <button type="button" onClick={e => handleqty("inc")} class="btn btn-outline-success mx-4">+1</button>
                      </>
                  }
                </div>
                <button onClick={handleAddToCart} type="button" class="btn btn-success w-100 my-3">Add To Cart</button>
              </div>
              <div className="col-sm-8">
                <h1>{singleProduct.name}</h1>
                <p>{parse(singleProduct.desc)}</p>
              </div>
            </div>
          </div>

        </div>}


    </div>

    {/* <div class="btn-group w-100" role="group">
                                        <button type="button" onClick={e => qty > 0 ? setqty(qty - 1) : qty == 1} class="btn btn-outline-danger btn-outline mx-4">-1</button>
                                        <h1>{+singleProduct.stock + qty}</h1>
                                        <button type="button" onClick={e => setqty(qty + 1)} class="btn btn-outline-success mx-4">+1</button>
    
                                    </div> */}
    {/* pagination */}



  </>

}