import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { placeOrderAction } from '../store.jsx/products/OrderAction'

export default function Payment() {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const { cartItems } = useSelector(state => state.cart)
  const { orderPlaceSuccess } = useSelector(state => state.allOrders)

  const { userlogin } = useSelector(state => state.alluser)

  useEffect(() => {
if(orderPlaceSuccess){

  navigate("/success")
}
  }, [orderPlaceSuccess])

  return <>
    <div className="PlaceOrder">
      <button onClick={e => {
        dispatch(placeOrderAction({
          products: cartItems,
          status: "placed",
          userId: userlogin.id
        }))
      }} type="button" class="btn btn-outline-warning">Place Order</button>

    </div>
  </>
}
