import React from 'react'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Card from 'react-bootstrap/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useState } from 'react';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
import { useDispatch, useSelector } from 'react-redux';
import addProductAction from '../../store.jsx/products/productAction';

// rich text editor
export default function Products() {
    const dispatch = useDispatch()
    const [desc, setdesc] = useState("Business laptops that are #ACutAbove. Powered by 12th Gen Intel® Core™ processors. Perform at your best anytime, anywhere, with new cutting-edge Japanese technology. Powerful and Portable. #GoLighter with Dynabook....")
    const [productData, setproductData] = useState({
        name: "Dell Xps",
        image: "https://images.unsplash.com/photo-1496181133206-80ce9b88a853?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2071&q=80",
        stock: "5",
        price: "9999",
        publish: true,
        category: "laptop"
    })
    const handleSubmit = e => {
        e.preventDefault()
        dispatch(addProductAction({ ...productData, desc }))
        // console.log(handleSubmit);
    }
    return (<>
        <pre>{JSON.stringify(productData, null, 2)}</pre>
        <pre>{JSON.stringify(desc, null, 2)}</pre>
        <Container>
            <Row>
                <Col className='col-sm-6 offset-sm-3'  >
                    <Card style={{ width: '35rem' }}>
                        <Card.Img variant="top" src="" />
                        <Card.Header>  <Card.Title> products</Card.Title></Card.Header>
                        <Card.Body>
                            <Form onSubmit={handleSubmit}>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>Enter Product Name</Form.Label>
                                    <Form.Control value={productData.name} onChange={e => setproductData({ ...productData, name: e.target.value })} type="text" placeholder="Enter name" />
                                    <Form.Text className="text-muted">
                                        Enter your product name
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <ReactQuill theme="snow" value={desc} onChange={setdesc} />
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>enter image</Form.Label>
                                    <Form.Control value={productData.image} onChange={e => setproductData({ ...productData, imag: e.target.value })} type="text" placeholder="Enter image" />
                                    <Form.Text className="text-muted">
                                        product image
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>enter stock</Form.Label>
                                    <Form.Control value={productData.stock} onChange={e => setproductData({ ...productData, stock: e.target.value })} type="stock" placeholder="Enter stock" />
                                    <Form.Text className="text-muted">
                                        product stock
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>enter quantity</Form.Label>
                                    <Form.Control value={productData.quantity} onChange={e => setproductData({ ...productData, quantity: e.target.value })} type="quantity" placeholder="Enter quantity" />
                                    <Form.Text className="text-muted">
                                        product quantity
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group className="mb-3" controlId="formBasicEmail">
                                    <Form.Label>enter price</Form.Label>
                                    <Form.Control value={productData.price} onChange={e => setproductData({ ...productData, price: e.target.value })} type="price" placeholder="Enter Price" />
                                    <Form.Text className="text-muted">
                                        product price
                                    </Form.Text>
                                </Form.Group>
                                <Form.Select value={productData.category} onChange={e => setproductData({ ...productData, category: e.target.value })}>

                                    <option> Category</option>
                                    <option value="laptop">laptop</option>
                                    <option value="mobile">Mobile</option>
                                    <option value="tab">Tab</option>
                                </Form.Select>
                                <Button variant="primary" type="submit">
                                    Submit
                                </Button>
                            </Form>
                        </Card.Body>
                    </Card>


                </Col>
            </Row>
        </Container>

    </>

    )
}