import React, { useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { emptyCart, removeItems } from '../store.jsx/products/cartSlice'


export default function Cart() {
  // const [estCost, setestCost] = useState()
  // const [gst, setgst] = useState()
  
  const dispatch =useDispatch()
  const { cartItems } = useSelector(state => state.cart)
const estCost =cartItems.reduce((acc,item)=> acc += (item.price*item.qty),0)
const gst =(cartItems.reduce((acc, item) => acc += item.qty * item.price, 0) * 0.28).toFixed(2)
 
  return <>
    {
      cartItems.length == 0
        ? <h1>Cart Is Empty</h1>
        : <div className="container">
          <button onClick={e => dispatch(emptyCart())} type="button" class="btn btn-outline-primary my-4">Empty Cart</button>
          <div className="row">
            <div className="col-sm-8">
              {
                cartItems.map(product => <> <div className="card">
                  <div className="card-body">
                    <div className='d-flex justify-content-around align-items-center' >
                      <img src={product.image} className='img-fluid w-50' alt={product.name} />
                      <div>
                        <h3>{product.name}</h3>
                        <p>price:{product.price}</p>
                        <p>Quantity:{product.qty}</p>
                        <h2>Total :{product.price * product.qty}</h2>
                        <hr />
                        <button  onClick={e =>dispatch(removeItems())} type="button" class="btn btn-outline-primary">Remove</button>
                      </div>
                    </div>
                  </div>
                </div>
                </>)
              }

            </div>
            <div className="col-sm-4">
              
              {/* price details */}
              <div class="card">
                <div class="card-header">Summary</div>
                <div class="card-body">body</div>
                <h3>Total Products {cartItems.reduce((acc,item)=> acc += item.qty,0)}</h3>
                <h3>Estimated  {estCost} </h3>
              <h3>GST {gst}</h3>
                <h3>Payble Amount: {((+estCost)+(+gst))}</h3>
                <div className="card-footer">
                <Link  class="btn btn-success w-100" to ="/payment">CheckOut</Link>
              </div>
              </div>
             </div>
          </div>
        </div>
    }
    <div className="text-align-center">

    </div>
  </>
}
