import React, { useEffect } from 'react'
import Col from 'react-bootstrap/esm/Col'
import Container from 'react-bootstrap/esm/Container'
import Row from 'react-bootstrap/esm/Row'
import Card from 'react-bootstrap/esm/Card'
import { Form, Button, Alert } from "react-bootstrap"
import { useFormik } from "formik"
import * as yup from "yup"
import axios from 'axios'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer, toast } from 'react-toastify';
import { useState } from 'react'
import { removeUserAdded } from '../store.jsx/products/userSlice'
import { addUserAction, getUserAction } from '../store.jsx/products/userAction'

export default function Register() {
    const [show, setshow] = useState(false)
    const [error, seterror] = useState()
    const { userAdded } = useSelector(state => state.alluser)
    const dispatch = useDispatch()
    const formik = useFormik({
        initialValues: {
            name: "kate",
            email: "kate@gmail.com",
            avatar: "https://images.unsplash.com/photo-1496181133206-80ce9b88a853?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2071&q=80",
            password: "123",
            cpassword: "123",
            activeuser: true
        },
        validationSchema: yup.object({
            name: yup.string().required("please enter your name"),
            email: yup.string().required().email("please enter valid mail"),
            avatar: yup.string().required("please enter your image"),
            // activeuser:true
            password: yup.string().required("please enter password").min(2, "please enter minimum two characters"),
            cpassword: yup.string().required("please confirm password").oneOf([yup.ref("password")], "password do not match"),
            activeuser: yup.string()
        }),
        onSubmit: async (values, { resetForm }) => {
            try {
                // console.log(values);
                setshow(true)
                dispatch(addUserAction(values))

            } catch (error) {
                seterror(error.message)
                // return error.message
            }
        }
    })
    useEffect(() => {

        if (userAdded) {
            toast.success("users added succesfully")
        }
        dispatch(removeUserAdded())
    }, [userAdded])

    return <>
        {/* <ToastContainer/> */}
        <Container>
            <Row>
                <Col className='col-sm-6 offset-3' >
                    <Card>
                        <Card.Header>
                            {
                                show && <Alert>
                                    {formik.values.name} Registered successfully
                                </Alert>
                            }

                            {
                                error && <Alert variant='danger' >
                                    {error}
                                </Alert>
                            }


                            Registration Form</Card.Header>
                        <Card.Body>
                            <Form onSubmit={formik.handleSubmit}  >

                                <Form.Group>
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="Enter name"
                                        name="name"
                                        onChange={formik.handleChange}
                                        value={formik.values.name}
                                        onBlur={formik.handleBlur}
                                        isInvalid={formik.touched.name && formik.errors.name}
                                        isValid={formik.touched.name && !formik.errors.name}
                                    />

                                    <Form.Text className="invalid-feedback">
                                        {formik.errors.name}
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="Enter email"
                                        name="email"
                                        onChange={formik.handleChange}
                                        value={formik.values.email}
                                        onBlur={formik.handleBlur}
                                        isInvalid={formik.touched.email && formik.errors.email}
                                        isValid={formik.touched.email && !formik.errors.email}
                                    />
                                    <Form.Text className="invalid-feedback">
                                        {formik.errors.email}
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>Avatar</Form.Label>
                                    <Form.Control
                                        type="text"
                                        placeholder="Enter avatar"
                                        name="avatar"
                                        onChange={formik.handleChange}
                                        value={formik.values.avatar}
                                        onBlur={formik.handleBlur}
                                        isInvalid={formik.touched.avatar && formik.errors.avatar}
                                        isValid={formik.touched.avatar && !formik.errors.avatar}
                                    />
                                    <Form.Text className="invalid-feedback">
                                        {formik.errors.avatar}
                                    </Form.Text>
                                </Form.Group>

                                <Form.Group>
                                    <Form.Label>Password</Form.Label>
                                    <Form.Control
                                        type="password"
                                        placeholder="Enter password"
                                        name="password"
                                        onChange={formik.handleChange}
                                        value={formik.values.password}
                                        onBlur={formik.handleBlur}
                                        isInvalid={formik.touched.password && formik.errors.password}
                                        isValid={formik.touched.password && !formik.errors.password}
                                    />
                                    <Form.Text className="invalid-feedback">
                                        {formik.errors.password}
                                    </Form.Text>
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>  Confirm  Password</Form.Label>
                                    <Form.Control
                                        type="password"
                                        placeholder=" Confirm  password"
                                        name="cpassword"
                                        onChange={formik.handleChange}
                                        value={formik.values.cpassword}
                                        onBlur={formik.handleBlur}
                                        isInvalid={formik.touched.cpassword && formik.errors.cpassword}
                                        isValid={formik.touched.cpassword && !formik.errors.cpassword}
                                    />
                                    <Form.Text className="invalid-feedback">
                                        {formik.errors.cpassword}
                                    </Form.Text>
                                </Form.Group>


                                <div class="form-check form-switch my-3">
                                    <input
                                        class="form-check-input"
                                        value={formik.values.activeuser}
                                        checked={formik.values.activeuser}
                                        name="activeuser"
                                        onChange={formik.handleChange}
                                        type="checkbox"
                                        role="switch" id="flexSwitchCheckDefault" />
                                    <label class="form-check-label" for="flexSwitchCheckDefault">active</label>
                                </div>



                                <Button className='mt-4' variant="primary" type="submit"  >
                                    Submit
                                </Button>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    </>

}
