import { createSlice } from "@reduxjs/toolkit"
import addProductAction, { getAllProductAction, getSingleProductAction } from "./productAction"


const productSlice = createSlice({
    name: "products",
    initialState: { products: [] },
    reducers: {},
    extraReducers(builder) {
        builder
            .addCase(addProductAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(addProductAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.productAdded = true
            })
            .addCase(addProductAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })

            .addCase(getAllProductAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getAllProductAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.products = payload
            })
            .addCase(getAllProductAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })


            // for details page
            .addCase(getSingleProductAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getSingleProductAction.fulfilled, (state, { payload }) => {
                state.loading = false
                // payload && (state.login = true)
                state.singleProduct = payload
                
            })
            .addCase(getSingleProductAction.rejected, (state, { payload }) => {
                state.loading = false
                state.singleProductError = payload
            })
    }
})
export default productSlice.reducer