import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

export const placeOrderAction = createAsyncThunk("order/place", async orderData => {
    try {
        const { data } = await axios.post("/orders", orderData)
        return data
    } catch (error) {
        return error.message
    }
})
export const getAllOrdersAction = createAsyncThunk("order/get", async (id, { rejectWithValue, getState }) => {
    try {
        const { data } = await axios.get("/orders")
        const filterOrders = data.filter(item => item.userId == id)
        return filterOrders
    } catch (error) {
        return rejectWithValue(error.message)
    }
})

export const updateOrderStatusAction = createAsyncThunk("order/updates", async ({ status, id }, { rejectWithValue, getState }) => {
    try {
        const { data } = await axios.patch(`/orders/${id}`, { status })
        // const { data } = await axios.put(`/orders/${order.id}`, { status })
        return data
    } catch (error) {
        return rejectWithValue(error.message)
    }
})

