import { createSlice } from "@reduxjs/toolkit";
import { changeStatusAction, dashboardDataAction } from "./dashboardAction";
const dashboardSlice = createSlice({
    name: "orderData",
    initialState: { dashboardData: [] },
    reducers: {},
    extraReducers(builder) {
        builder
            // for registered user
            .addCase(dashboardDataAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(dashboardDataAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.dashboardData = payload

            })
            .addCase(dashboardDataAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload   
            })
            .addCase(changeStatusAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(changeStatusAction.fulfilled, (state, { payload }) => {
                state.loading = false

            })
            .addCase(changeStatusAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })
    }
})
export default dashboardSlice.reducer