import { createAsyncThunk } from "@reduxjs/toolkit"
import axios from "axios"
const addProductAction = createAsyncThunk("Products/add",
    async productData => {
        try {
            const { data } = await axios.post("/products", productData)
            return data
        } catch (error) {
            return error.message
        }
    })
export const getAllProductAction = createAsyncThunk("Products/get",
    async () => {
        try {
            const { data } = await axios.get("/products")
            return data
        } catch (error) {
            return error.message
        }
    })

export const getSingleProductAction = createAsyncThunk("products/getSingleProduct", async (id, { rejectWithValue, getState }) => {
    try {
        const { data } = await axios.get(`/products/${id}`)
        // console.log(data);
        return data
    } catch (error) {
        return rejectWithValue(error.message)
    }
})
export default addProductAction