import { createSlice } from "@reduxjs/toolkit";
import { getAllOrdersAction, placeOrderAction,  updateOrderStatusAction } from "./OrderAction";

const OrderSlice = createSlice({
    name: "orders",
    initialState: { orderData: [], orders: [] },
    reducers: {},
    extraReducers(builder) {
        builder
            .addCase(placeOrderAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(placeOrderAction.fulfilled, (state) => {
                state.loading = false
                state.orderPlaceSuccess = true
            })
            .addCase(placeOrderAction.rejected, (state, { payload }) => {
                state.loading = false
                state.orderError = payload
            })
            
            .addCase(getAllOrdersAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getAllOrdersAction.fulfilled, (state, { payload }) => {
                state.orders = payload
            })
            .addCase(getAllOrdersAction.rejected, (state, { payload }) => {
                state.loading = false
                state.orderError = payload
            })



            .addCase(updateOrderStatusAction.pending, (state, { payload }) => {
                state.loading = true
                state.statusUpdate = false
            })
            .addCase(updateOrderStatusAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.statusUpdate = true
            })
            .addCase(updateOrderStatusAction.rejected, (state, { payload }) => {
                state.loading = false
                state.updateError = payload
            })
    }
})
export default OrderSlice.reducer
export const selectOrder = state => state.allOrders