import { configureStore } from "@reduxjs/toolkit"
import cartSlice from "./products/cartSlice"
import cmsSlice from "./products/cmsSlice"
import DashboardSlice from "./products/DashboardSlice"
import OrderSlice from "./products/OrderSlice"
import productSlice from "./products/productSlice"
import userSlice from "./products/userSlice"

const store = configureStore({
    reducer: {
        allProducts: productSlice,
        alluser: userSlice,
        cart: cartSlice,
        allOrders: OrderSlice,
        allcms: cmsSlice
    }

})
export default store