import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";


export const dashboardDataAction = createAsyncThunk("order/data", async (orderId, { rejectWithValue, getState }) => {
    try {
        const { data } = await axios.get("/orders")
        const orderUser = data.filter(item => item.userId === orderId)
        return orderUser
    } catch (error) {
        return error.message
    }
})

export const changeStatusAction = createAsyncThunk("order/status", async ({ rejectWithValue, getState }) => {
    try {
        const { data } = await axios.get("/orders/id")

        return data
    } catch (error) {
        return error.message
    }
})

