import { createSlice } from "@reduxjs/toolkit";
import { getcmsAction } from "./cmsAction";


const cmsSlice = createSlice({
    name: "cms",
    initialState: { cmsdata: [] },
    reducers: {},
    extraReducers(builder) {
        builder
            // for registered user
            .addCase(getcmsAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getcmsAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.cmsdata = payload

            })
            .addCase(getcmsAction.rejected, (state, { payload }) => {
                state.loading = false
                state.cmcError = payload
            })
    }
})
export default cmsSlice.reducer
export const selectcms = state => state.allcms