import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from 'axios'


export const getUserAction = createAsyncThunk("user/registered", async userdata => {
    try {
        const { data } = await axios.get("/users")
        return data
    } catch (error) {
        return error.message
    }
})
export const addUserAction = createAsyncThunk("users/registerData", async userdata => {
    try {
        const { data } = await axios.post("/users", userdata)
        return data
    } catch (error) {
        return error.message
    }
})
export const userLoginAction = createAsyncThunk("users/loginData", async (loginData, { rejectWithValue, getState }) => {
    try {
        const { data } = await axios.get("/users")
        const found = data.find(item => item.email === loginData.email && item.password === loginData.password)
        if (found) {

            localStorage.setItem("login", JSON.stringify(found))
            return found
        } else {
            return rejectWithValue("email Or password wrong")
        }

    } catch (error) {
        return rejectWithValue(error.message)
    }
})
