import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import Table from 'react-bootstrap/Table';
import { Container } from 'react-bootstrap';
import { addUserAction, getUserAction } from '../../store.jsx/products/userAction';
;


export default function Users() {
  const dispatch = useDispatch()
  const { userdata } = useSelector(state => state.alluser)
  useEffect(() => {
    const promise = dispatch(getUserAction())
    return () => {
      promise.abort()
    }
  }, [])
  return <>
    {/* {JSON.stringify(userdata)} */}
    <Container>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th> Name</th>
            <th>Email</th>
            <th>avatar</th>
            <th>active user</th>
          </tr>
        </thead>
        <tbody>

          {
            userdata.map((item, index) => <tr>
              <td>{index + 1}</td>
              <td>{item.name}</td>
              <td>{item.email}</td>


              <td> <img src={item.avatar}
                className="rounded-circle me-2 bg-secondary"
                height={50}
                width={50}
                alt="" referrerPolicy="no-referrer" /></td>

              {/* <td>{item.password}</td> */}
              <td>{item.activeuser ? "active" : "inactive"}</td>
            </tr>)
          }

        </tbody>
      </Table>
    </Container>

  </>
}
