import React from 'react'
import { useSelector } from 'react-redux'
import { array } from 'yup/lib/locale'
import { selectOrder } from '../../../store.jsx/products/OrderSlice'
import { selectUsers } from '../../../store.jsx/products/userSlice'

export default function Stat() {
    const { orders } = useSelector(selectOrder)
    const { users } = useSelector(selectUsers)
    const arr = [
        { title: "Total", num: orders.length },
        { title: "placed", num: orders.filter(item => item.status == "placed").length },
        { title: "delivered", num: orders.filter(item => item.status == "delivered").length },
        { title: "dispatch", num: orders.filter(item => item.status == "dispatch").length },
    ]
    return <>
        <div className="container">
            <div className="row">
                {
                    arr.map(item => <div className="col-sm-3">
                        <div class="card shadow-lg">
                            <div class="card-body p-2">
                                <h2>{item.title} {item.num}</h2>
                            </div>
                        </div>
                    </div>)
                }
            </div>
        </div>
    </>
}
