import React from 'react'
import { BrowserRouter, Route, Routes, Link } from 'react-router-dom'
import Topbar from './components/Topbar'
import { PageNotFound, Protected } from './pages'
import { Dashboard, Products, Users } from './pages/admin'
import Orders from './pages/admin/Orders'
import Cart from './pages/Cart'
import Details from './pages/Details'
import Home from './pages/Home'
import Login from './pages/Login'
import Order from './pages/Order'
import Payment from './pages/Payment'
import Register from './pages/Register'
import { Account, Profile } from './pages/user'
import Histroy from './pages/user/Histroy';
import 'bootstrap/dist/css/bootstrap.min.css';
import { ToastContainer } from 'react-bootstrap'
import Success from './pages/Success'
import AdminOnly from './pages/admin/AdminOnly'
import Test from './pages/Test'
import HomeCMS from './pages/admin/CMS/HomeCMS'
import Cms from './pages/admin/CMS/Cms'

export default function App() {
  return <BrowserRouter>
    <Topbar />
    <ToastContainer />
    <Routes>
      <Route path="/" element={<Test />}></Route>
      {/* <Route path="/test" element={<Test />}></Route> */}
      <Route path="/login" element={<Login />}></Route>
      <Route path="/register" element={<Register />}></Route>
      <Route path="/details/:productId" element={<Details />}></Route>
      <Route path="/cart" element={<Cart />}></Route>
      <Route path="/payment" element={<Protected element={<Payment />} />}></Route>
      <Route path="/order" element={<Order />}></Route>
      <Route path="/success" element={<Success />}></Route>
      <Route path="*" element={<PageNotFound />}></Route>
      {/* user */}
      <Route path="user/account" element={<Account />}></Route>
      <Route path="user/history" element={<Histroy />}></Route>
      <Route path="user/profile" element={<Profile />}></Route>

      {/* Admin */}
      <Route path="/admin/dashboard" element={<AdminOnly element={<Dashboard />} />}></Route>
      <Route path="/admin/products" element={<AdminOnly element={<Products />} />}></Route>
      <Route path="/admin/order" element={<AdminOnly element={<Orders />} />}></Route>
      <Route path="/admin/users" element={<AdminOnly element={<Users />} />}></Route>
      <Route path="/admin/cms/home" element={<AdminOnly element={<HomeCMS />} />}></Route>
      <Route path="/admin/cms" element={<AdminOnly element={<Cms />} />}></Route>

    </Routes>
    <ToastContainer />
  </BrowserRouter>
}
