
import React, { useEffect, useState } from 'react'
import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { selectOrder } from '../../store.jsx/products/OrderSlice'
import { selectUsers } from '../../store.jsx/products/userSlice'
import { addUserAction, getUserAction } from '../../store.jsx/products/userAction'
import { dashboardDataAction } from '../../store.jsx/products/dashboardAction'
import { changeStatusAction } from "../../store.jsx/products/dashboardAction"
import { getAllOrdersAction, updateOrderStatusAction } from '../../store.jsx/products/OrderAction'
import { toast } from 'react-toastify'
import Search from 'antd/lib/transfer/search'
import Stat from './components/Stat'

export default function Dashboard() {
  const dispatch = useDispatch()
  const { userdata } = useSelector(selectUsers)
  const { orders, statusUpdate } = useSelector(selectOrder)
  const [selectedOrder, setSelectedOrder] = useState()
  const [selectedStatus, setselectedStatus] = useState("all")
  const [filterOrders, setfilterOrders] = useState([])
  const [value, setvalue] = useState()
  const [click, setclick] = useState([])
  useEffect(() => {
    const userPromise = dispatch(getUserAction())
    // const orderPromise = dispatch(getAllOrdersAction())
    return () => {
      // userPromise.abort()
      // orderPromise.abort()
    }

  }, [])
  useEffect(() => {
    userdata && setclick(userdata)
  }, [userdata])
  useEffect(() => {
    if (statusUpdate) {
      toast.success("Status Updated Success")
    }

  }, [statusUpdate])

  useEffect(() => {
    // console.log(filterOrders);
    // console.log(selectedStatus);

    // console.log(orders.filter(item => item.status == "dispatch"));

    // 
    if (filterOrders === 0) {
      setfilterOrders(orders.filter(item => item.status == "all"))
    } else {
      setfilterOrders(orders)
    }
  }, [orders, selectedStatus])

  const handleChange = () => {
    const filterData = userdata.filter(item => item.name.includes(value))
    setclick(filterData)
    // console.log(click);
  }

  return (<>

    <pre>
      {JSON.stringify(click)}
    </pre>

    <Stat />
    <hr />
    <div className="container">
      <div className="row">
        <div className="col-sm-4">
          <div class="card">
            <div class="card-header">header</div>
            <input type="text" placeholder='search' onChange={e => setvalue(e.target.value)} />
            {/* <button onClick={e => handleChange()} type="button" class="btn btn-primary">search</button> */}
            <div class="card-body">
              <ul className='list-group' >
                {
                  click.map((item, index) => <>
                    <li
                      className='list-group-item d-flex justify-content-between' >
                      <img src={item.avatar} alt="" srcset="" height="50" width="50" />
                      <strong>{item.name}</strong>
                      <button onClick={e => dispatch(getAllOrdersAction(item.id))}>Show</button>
                    </li>
                  </>)
                }
                {/* {
                  userdata.map((item, index) => <>
                    <li

                      className='list-group-item d-flex justify-content-between' >
                      <img src={item.avatar} alt="" srcset="" height="50" width="50" />
                      <strong>{item.name}</strong>
                      <button onClick={e => dispatch(getAllOrdersAction(item.id))}>Show</button>
                    </li>
                  </>)
                } */}
              </ul>
            </div>

          </div>
        </div>



        <div className="col-sm-4">
          {
            filterOrders.length === 0
              ? <p >Please Select Order</p>
              : <>
                <div class="card my-3">
                  <div class="card-body">
                    <select class="form-select" value={selectedStatus} onChange={e => setselectedStatus(e.target.value)}>
                      <option value="placed">placed</option>
                      <option value="delivered">delivered</option>
                      <option value="dispatch">dispatch</option>
                    </select>
                  </div>

                </div>
                {

                }
                {
                  filterOrders.map((item, index) => <>
                    {

                      <div class="card">
                        <div class="card-header">Order Status{item.status}
                          <button type="button" class="btn btn-success" onClick={e => setfilterOrders(item)}>Edit</button></div>
                        <div class="card-body">
                          <ul>
                            {
                              item.products.map(product =>
                                <>
                                  <li class="list-group-item d-flex justify-content-between text-light"></li>
                                  <li>
                                    <img src={product.image} alt={product.name} height="50" width="50" />
                                    <p>ID ={product.id}</p>
                                    <span>{product.name}</span>
                                    <span>{product.qty} X {product.price}</span>
                                  </li>
                                </>)
                            }
                          </ul>
                        </div>
                        <div>
                        </div>
                      </div>
                    }
                  </>

                  )
                }
              </>
          }
        </div>

        <div className="col-sm-4">
          <pre>

            {JSON.stringify(selectOrder, null, 2)}
          </pre>
        </div>
        {
          setSelectedOrder
            ? <><select class="form-select" onChange={e => setSelectedOrder({ ...selectedOrder, status: e.target.value })}>

              <option value="placed">placed</option>
              <option value="dispatch">dispatch</option>
              <option value="delivered">delivered</option>
            </select>
              <button onClick={e => dispatch(updateOrderStatusAction(selectedOrder))} type="button" class="btn btn-primary w=100 btn-lg mt-2">Update Status</button></>
            : <>No Order Selected</>
        }
      </div>
    </div>
  </>
  )
}
