import React from 'react'
import { useSelector } from 'react-redux'
import { Navigate } from 'react-router-dom'
import { selectUsers } from '../../store.jsx/products/userSlice'

export default function AdminOnly({ element }) {
    const { userlogin } = useSelector(selectUsers)
    return userlogin && userlogin.admin ? element : <Navigate to="/login"></Navigate>
}