import { createSlice } from "@reduxjs/toolkit";
import { addUserAction, getUserAction, userLoginAction } from "./userAction";
const localLoginData = localStorage.getItem("login")
    ? JSON.parse(localStorage.getItem("login"))
    : null
const userSlice = createSlice({
    name: "users",
    initialState: { userdata: [], userlogin: localLoginData },
    reducers: {
        removeUserAdded(state) {
            state.userAdded = false
        },
        resetLoginError(state) {
            state.loginError = undefined
        },
        logout(state) {
            localStorage.removeItem("login")
            state.userlogin = null
        }
    },
    extraReducers(builder) {
        builder
            // for registered user
            .addCase(getUserAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(getUserAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.userdata = payload

            })
            .addCase(getUserAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })
            // to print userdata
            .addCase(addUserAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(addUserAction.fulfilled, (state, { payload }) => {
                state.loading = false
                state.userAdded = true
                // state.loginError = payload

            })
            .addCase(addUserAction.rejected, (state, { payload }) => {
                state.loading = false
                state.error = payload
            })


            .addCase(userLoginAction.pending, (state, { payload }) => {
                state.loading = true
            })
            .addCase(userLoginAction.fulfilled, (state, { payload }) => {
                state.loading = false
                // payload && (state.login = true)
                state.userlogin = payload

            })
            .addCase(userLoginAction.rejected, (state, { payload }) => {
                state.loading = false
                state.loginerror = payload
            })



    }

})
export default userSlice.reducer
export const selectUsers = state => state.alluser
export const { removeUserAdded, resetLoginError, logout } = userSlice.actions